export default {
	name: "spec",
	props: {
		// 默认picker选中项索引
		spesData: {
			required: true
		},
		indexes: {
			required: true
		}
	},
	data() {
		return {
			indexs:[]
		}
	},
	methods: {
		specChangeSpes(index,inde) {
			this.indexs = [0]
			this.indexs = this.indexes
			this.indexs[index] = inde
			this.$emit("changeSpes", JSON.stringify(this.indexs));
		}
	},
	created(){
		this.indexs = this.indexes
	},
}
